@import "../../../../App.scss";

.table-container {
    // padding: 20px;
    // height: Fixed (840px);
    top: 164px;
    left: 120px;
    border-radius: 25px;
    border: 0.5px solid var(--quinary-color);
    // gap: 15px;
    background-color: var(--secondary-color);
    // display: flex;
    flex-direction: column;
    padding: 8px 5px 10px 0px;
    width: 50%;
    height: calc(100% - 200px);
    box-shadow: 2px 2px 4px 0px #00000033;
}

.table-title {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px 10px 10px;
    font-size: 22px;
}

.table-username-cell {
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
    @extend .medium-18;
}

.table-profile-image {
    width: 42px;
    height: 42px;
    display: flex;
    border-radius: 50%;
    padding: 5px;
}

.table-profile-image img {
    box-shadow: 2px 2px 4px 0px #00000033;
    border-radius: 50%;
    object-fit: cover;
}

.header-label {
    @extend .medium-18;
    padding: 5px;
    align-items: center;
}

.search-bar-container {
    width: 50%;
}

.title-text {
    padding: 5px 10px;
}

.trasfer-to-dd {
    width: 100%;
}