@import '../../../App.scss';

p {
    padding: 0px 5px;
    gap: 10px;
}

.dropdown {
    width: 50%;
    position: relative;
    display: flex;
    align-items: center;
    // justify-content: space-between;
    margin-bottom: 10px;
}

.segment-view {
    padding: 10px 0;
    width: 50%;
    height: 40px;
    margin-bottom: 10px;
}

.segment-view .segment-label {
    display: flex;
    align-items: center;
    gap: 10px;
}

.task-assignment-landing {
    display: flex;
    gap: 20px;
    height: 100%;
}

.report_button_container {
    display: flex;
    justify-content: flex-end;
    cursor: pointer;
    width: 220px;
    @extend .medium-16;
}

.report_button {
    width: 220px;
    display: flex;
    justify-content: space-evenly;
    background-color: var(--tertiary-color);
    height: 40px;
    color: var(--secondary-color);
    border-radius: 15px;
    box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
}

.width-50 {
    width: 50%;
}

.button {
    color: var(--secondary-color) !important;
    text-transform: none !important;
    @extend .semiBold-16;
}

.selected-list-view {
    background-color: var(--secondary-color);
    height: 300px;
    box-shadow: 2px 2px 4px 0px rgba(0, 0, 0, 0.2);
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-top: 20px;
    padding-left: 20px;
}

.header-label {
    border-bottom: 1px solid;
    width: 25%;
    font-size: 22px;
}

.transfer-to-view {
    display: flex;
    flex-direction: column;
    width: 50%;
}

.landing-page-task {
    height: 100%;
}